<template>
    <div>
         <!-- 面包屑导航 -->
        <el-breadcrumb separator="/">
          <el-breadcrumb-item :to="{ path: '/welcome' }">首页</el-breadcrumb-item>
          <el-breadcrumb-item :to="{ path: '/my/myclient' }">我的客户</el-breadcrumb-item>
          <el-breadcrumb-item>客户跟进</el-breadcrumb-item>
        </el-breadcrumb>
        <!-- 卡片视图区 -->
        <el-card>
            <!-- 卡片头部 -->
            <div slot="header" class="clearfix">
              <span>客户跟进</span>
              <el-button style="float: right; padding: 3px 0" type="text" icon="el-icon-d-arrow-left" @click="returnHandle">返回</el-button>
            </div>
             <el-row>
                <el-col :span="3">
                     <el-button type="primary" icon="el-icon-circle-plus-outline" size="middle" plain @click="createUpdateHandle">创建跟进</el-button>
                </el-col>
                <!-- <el-col :span="6">
                    <el-input placeholder="搜索客户"  v-model="searchInputValue">
                      <i slot="prefix" class="el-input__icon el-icon-search"></i>
                    </el-input>
                </el-col> -->
              </el-row>
            <!-- 卡片内容区 -->
            <el-table :data="tableData" stripe style="width: 100%" border>
              <el-table-column prop="followUpRecordUpdateDate"  label="跟进时间" width="180">  </el-table-column>
              <el-table-column  prop="followUpRecordType"  label="跟进类型"  width="180">
                      <template slot-scope="scope">
                        <el-tag type="danger" v-if="scope.row.followUpRecordType===0">电话</el-tag>
                        <el-tag type="success" v-else-if="scope.row.followUpRecordType===1">微信</el-tag>
                        <el-tag type="info" v-else>阿里旺旺</el-tag>
                      </template>
              </el-table-column>
              <el-table-column  prop="followUpTheme"  label="主题"> </el-table-column>
              <el-table-column  prop="followUpContent"  label="跟进内容">
                 <template slot-scope="scope">
                   <div v-html="scope.row.followUpContent"></div>
                  </template>
              </el-table-column>
              <el-table-column  prop="nextRemindDate"  label="下次提醒时间">
                <template slot-scope="scope">
                  {{scope.row.nextRemindDate.split(' ')[0]}}
                </template>

              </el-table-column>
              <el-table-column  prop="followUpRecordCreateDate"  label="保存时间"> </el-table-column>
              <el-table-column  prop="opration"  label="操作">
                <template slot-scope="scope">
                  <el-button size="mini" type="primary" plain @click="updateDetailHandle(scope.row.followUpRecordId)">详情</el-button>
                </template>
              </el-table-column>
            </el-table>
            <!-- 卡片内容分页区 -->
            <!-- <el-pagination  :page-sizes="[100, 200, 300, 400]" :page-size="100" layout="total, sizes, prev, pager, next, jumper"  :total="400"> </el-pagination> -->
        </el-card>
    </div>
</template>
<script>
import Qs from 'qs'

export default {
  data () {
    return {
      tableData: []
    }
  },
  created () {
    // console.log(this.$route)
    this.getCustomerUpdate()
  },
  methods: {
    // 获取当前客户跟进数据
    async getCustomerUpdate () {
      const data = Qs.stringify({ customerId: this.$route.query.customerId })
      const { data: res } = await this.$http.post('/followUpRecord/findFollowUpRecordByCustomerId', data)
      this.tableData = res.data
      if (res.status !== 200) return this.$message.error('获取客户跟进列表失败！')
      // return this.$message.success('获取客户跟进列表成功！')
    },
    // 点击创建跟进按钮触发该事件
    createUpdateHandle () {
      // console.log(1)
      this.$router.push({ path: '/update/createUpdate', query: { customerId: this.$route.query.customerId, customerName: this.$route.query.customerName, userId: this.$route.query.userId, userName: this.$route.query.userName } })
    },
    // 点击跟进详情按钮触发该事件
    updateDetailHandle (followUpRecordId) {
      this.$router.push({ path: '/update/updateDetail', query: { id: followUpRecordId } })
    },
    // 点击返回触发该事件，返回上一页
    returnHandle () {
      this.$router.push('/my/myclient')
    }
  }
}
</script>
<style lang="less" scoped>

</style>
