<template>
    <div>
         <!-- 面包屑导航 -->
        <el-breadcrumb separator="/">
          <el-breadcrumb-item :to="{ path: '/welcome' }">首页</el-breadcrumb-item>
          <el-breadcrumb-item :to="{ path: '/my/myclient' }">我的客户</el-breadcrumb-item>
          <el-breadcrumb-item :to="{ path: '/update/customerUpdate',query:updateDetail }">客户跟进</el-breadcrumb-item>
          <el-breadcrumb-item>跟进详情</el-breadcrumb-item>
        </el-breadcrumb>
        <!-- 卡片视图区 -->
        <el-card>
            <!-- 卡片头部 -->
            <div slot="header" class="clearfix">
              <span>跟进详情</span>
               <el-button style="float: right; padding: 3px 0" type="text" icon="el-icon-d-arrow-left" @click="returnHandle">返回</el-button>
            </div>

            <div class="cardContent">
                <el-row :gutter="20">
                  <el-col :span="12">
                      <span class="textLeft">客户名称</span>
                  </el-col>
                   <el-col :span="12">
                      <span class="textRight textRightColor">{{updateDetail.customerName}}</span>
                  </el-col>
                </el-row>
                <el-row :gutter="20">
                  <el-col :span="12">
                      <span class="textLeft">跟进类型</span>
                  </el-col>
                   <el-col :span="12">
                      <span class="textRight">
                        <el-tag type="danger" v-if="updateDetail.followUpRecordType===0">电话</el-tag>
                        <el-tag v-else-if="updateDetail.followUpRecordType===1">微信</el-tag>
                        <el-tag  type="info" v-else>阿里旺旺</el-tag>
                      </span>
                  </el-col>
                </el-row>
                <el-row :gutter="20">
                  <el-col :span="12">
                      <span class="textLeft">跟进时间</span>
                  </el-col>
                   <el-col :span="12">
                      <span class="textRight">{{updateDetail.followUpRecordUpdateDate}}</span>
                  </el-col>
                </el-row>
                <el-row :gutter="20">
                  <el-col :span="12">
                      <span class="textLeft">跟进主题</span>
                  </el-col>
                   <el-col :span="12">
                      <span class="textRight">{{updateDetail.followUpTheme}}</span>
                  </el-col>
                </el-row>
                <el-row :gutter="20">
                  <el-col :span="12">
                      <span class="textLeft  ">跟进内容</span>
                  </el-col>
                   <el-col :span="12">
                      <div class="textRight textRightContent" v-html="updateDetail.nextFollowUpContent"></div>
                  </el-col>
                </el-row>
                <el-row :gutter="20">
                  <el-col :span="12">
                      <span class="textLeft">下次提醒时间</span>
                  </el-col>
                   <el-col :span="12">
                      <span class="textRight textRightColor">{{updateDetail.nextRemindDate?updateDetail.nextRemindDate.split(' ')[0]:''}}</span>
                  </el-col>
                </el-row>
                 <el-row :gutter="20">
                  <el-col :span="12">
                      <span class="textLeft">下次跟进主题</span>
                  </el-col>
                   <el-col :span="12">
                      <span class="textRight ">{{updateDetail.nextFollowUpTheme}}</span>
                  </el-col>
                </el-row>
                <el-row :gutter="20">
                  <el-col :span="12">
                      <span class="textLeft ">下次跟进内容</span>
                  </el-col>
                   <el-col :span="12">
                      <div class="textRight textRightContent" v-html="updateDetail.nextFollowUpContent"></div>
                  </el-col>
                </el-row>
            </div>
        </el-card>
    </div>
</template>
<script>
import Qs from 'qs'
export default {
  data () {
    return {
      updateDetail: {}

    }
  },
  created () {
    // this.updateDetail = this.$route.query
    this.getCustomerUpDetail()
  },
  methods: {
    async getCustomerUpDetail () {
      const data = Qs.stringify({ followUpRecordId: this.$route.query.id })
      const { data: res } = await this.$http.post('/followUpRecord/findFollowUpRecordById', data)
      if (res.status !== 200) return this.$message.error('获取客户详情失败！')
      this.updateDetail = res.data
    },
    // 点击返回触发该事件，返回上一页
    returnHandle () {
      this.$router.go(-1)
      // this.$router.push({ path: '/update/customerUpdate', query: this.updateDetail })
    }
  }
}
</script>

<style lang="less" scoped>
// 左侧文本颜色
@textLeftColor: #949393;
// 右侧文本着重变色
@textRightColor: #fdb42d;
.textLeft{
    position: relative;
    top:0 ;
    left: 330px;
    font-size: 16px;
    color: @textLeftColor;
}
.textRight{
    position: relative;
    top: 0;
    right: 100px;
    font-size: 14px;
}
.textRightColor{
    color: @textRightColor;
}
.el-row {
    margin-bottom: 20px;
}
.textRightContent{
  border:1px solid #ccc;
  width: 300px;
  height: 100%;
  border-radius: 5px;
  padding: 25px 20px;
  line-height: 25px;
}
</style>
