<template>
    <div>
         <!-- 面包屑导航 -->
        <el-breadcrumb separator="/">
          <el-breadcrumb-item :to="{ path: '/welcome' }">首页</el-breadcrumb-item>
          <el-breadcrumb-item :to="{ path: '/my/myclient' }">我的客户</el-breadcrumb-item>
          <el-breadcrumb-item :to="{ path: '/update/customerUpdate',query:msg }">客户跟进</el-breadcrumb-item>
          <el-breadcrumb-item>创建跟进</el-breadcrumb-item>
        </el-breadcrumb>
        <!-- 卡片视图区 -->
        <el-card>
            <!-- 卡片头部 -->
            <div slot="header" class="clearfix">
              <span>跟进详情</span>
              <!-- <el-button style="float: right; padding: 3px 0" type="text" icon="el-icon-d-arrow-left" @click="returnHandle">返回</el-button> -->
            </div>
            <!-- 卡片内容区 -->
            <el-form :model="createUpdateRuleForm" :rules="createUpdateRules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
                <el-form-item label="跟进类型" prop="updateType">
                  <el-select v-model="createUpdateRuleForm.updateType" placeholder="请选择跟进类型">
                    <el-option label="电话" value="0"></el-option>
                    <el-option label="微信" value="1"></el-option>
                    <el-option label="阿里旺旺" value="2"></el-option>
                  </el-select>
                </el-form-item>
                <el-form-item label="跟进时间" required>
                  <el-col :span="5">
                    <el-form-item prop="date1">
                      <el-date-picker type="date" disabled laceholder="选择日期" v-model="createUpdateRuleForm.date1" style="width: 100%;"></el-date-picker>
                    </el-form-item>
                  </el-col>
                </el-form-item>
                 <el-form-item label="跟进主题" prop="theme1">
                  <el-input type="input" v-model="createUpdateRuleForm.theme1" class="textareaWidth"  ></el-input>
                </el-form-item>
                <el-form-item label="跟进内容" prop="desc1">
                  <el-input type="textarea" v-model="createUpdateRuleForm.desc1" class="textareaWidth" :autosize="{ minRows: 4 }" :placeholder="placeholderText" ></el-input>
                </el-form-item>
                 <el-form-item label="下次提醒时间" required>
                  <el-col :span="5">
                    <el-form-item prop="date2">
                      <el-date-picker type="date" placeholder="选择日期" v-model="createUpdateRuleForm.date2" style="width: 100%;"></el-date-picker>
                    </el-form-item>
                  </el-col>
                </el-form-item>
                <el-form-item label="下次跟进主题" prop="theme2">
                  <el-input type="input" v-model="createUpdateRuleForm.theme2" class="textareaWidth"  ></el-input>
                </el-form-item>
                <el-form-item label="下次跟进内容" prop="desc2">
                  <el-input type="textarea" v-model="createUpdateRuleForm.desc2" class="textareaWidth" :autosize="{ minRows: 4 }" :placeholder="placeholderText" ></el-input>
                </el-form-item>
                <el-form-item>
                  <el-button type="primary" @click="createUpdateSubmitForm('ruleForm')">提交</el-button>
                  <el-button @click="createUpdateResetForm('ruleForm')">重置</el-button>
                </el-form-item>
            </el-form>
        </el-card>
    </div>
</template>
<script>
import Qs from 'qs'

export default {
  data () {
    return {
      // 新增日报表单提交值
      createUpdateRuleForm: {
        updateType: '',
        date1: new Date(),
        date2: new Date(),
        desc1: '',
        desc2: '',
        theme1: '',
        theme2: '',
        // 最终格式化后的时间格式
        finallyDate1: '',
        finallyDate2: '',
        // 最终格式化后的文本格式
        finallyContent1: '',
        finallyContent2: ''
      },
      // 新增日报表单的预校验规则
      createUpdateRules: {
        updateType: [
          { required: true, message: '请选择跟进类型', trigger: 'change' }
        ],
        date1: [
          { type: 'date', required: true, message: '请选择日期', trigger: 'change' }
        ],
        date2: [
          { type: 'date', required: true, message: '请选择时间', trigger: 'change' }
        ],
        desc1: [
          { required: true, message: '请填写跟进内容', trigger: 'blur' }
        ],
        desc2: [
          { required: true, message: '请填写下次跟进内容', trigger: 'blur' }
        ],
        theme1: [
          { required: true, message: '请填写跟进主题', trigger: 'blur' }
        ],
        theme2: [
          { required: true, message: '请填写下次跟进主题', trigger: 'blur' }
        ]
      },
      // 表单文本域默认值
      placeholderText: '1.xxxxxxx\n2.xxxxxxx\n3.xxxxxxx',
      msg: {}
    }
  },
  created () {
    this.msg = this.$route.query
  },
  methods: {
    // 点击新增按钮弹出新增日报对话框，点击提交按钮时触发该事件
    createUpdateSubmitForm (formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          // 调用该事件将，将表单内格林时间格式的对象转化格式为 2019/11/22
          this.createUpdateRuleForm.finallyDate1 = this.timeChange(this.createUpdateRuleForm.date1)
          this.createUpdateRuleForm.finallyDate2 = this.timeChange(this.createUpdateRuleForm.date2)
          // 在表单提交信息内新增content，为最终参数
          this.createUpdateRuleForm.finallyContent1 = this.preText(this.createUpdateRuleForm.desc1)
          this.createUpdateRuleForm.finallyContent2 = this.preText(this.createUpdateRuleForm.desc2)
          const data = {
            UserId: this.$route.query.userId,
            UserName: this.$route.query.userName,
            CustomerId: this.$route.query.customerId,
            CustomerName: this.$route.query.customerName,
            FollowUpDate: this.createUpdateRuleForm.finallyDate1,
            FollowUpTheme: this.createUpdateRuleForm.theme1,
            FollowUpContent: this.createUpdateRuleForm.finallyContent1,
            NextRemindDate: this.createUpdateRuleForm.finallyDate2,
            NextFollowUpTheme: this.createUpdateRuleForm.theme2,
            NextFollowUpContent: this.createUpdateRuleForm.finallyContent2,
            FollowUpRecordType: this.createUpdateRuleForm.updateType
          }
          const data1 = Qs.stringify(data)
          const { data: res } = await this.$http.post('/followUpRecord/addFollowUpRecord', data1)
          if (res.status !== 200) return this.$message.error(res.msg)
          this.$router.push({ path: '/update/customerUpdate', query: this.msg })
          return this.$message.success(res.msg)
        } else {
          return false
        }
      })
    },
    // 点击新增按钮弹出新增日报对话框，点击重置按钮时触发该事件
    createUpdateResetForm (formName) {
      this.$refs[formName].resetFields()
    },

    //  自己分装的函数区域
    //  将格林时间格式的对象转化格式为 2019/11/22 11:25:50
    timeChange (time) {
      var time1 = time.getTime()
      var date = new Date(time1)
      var Y = date.getFullYear() + '/'
      var M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '/'
      var D = date.getDate()
      var current = Y + M + D
      return current
    },
    //  将文本域的文本进行处理让文本保存换行空格形式，传递给后端
    preText (pretext) {
      return pretext.replace(/\r\n/g, '<br/>').replace(/\n/g, '<br/>').replace(/\s/g, '&nbsp;')
    },
    // 点击返回按钮触发该事件，返回上一页
    returnHandle () {
      this.$router.push('/update/customerUpdate')
    }
  }
}
</script>
<style lang="less" scoped>
.textareaWidth{
    width:400px;
}
</style>
